//loading
.bootstrap-table .fixed-table-container .fixed-table-body .fixed-table-loading {
  background-color: var(--bs-light-bg-subtle) !important;
}

// 
.bootstrap-table .fixed-table-container.fixed-height .fixed-table-border {
  border-left: var(--bs-border-width) solid var(--bs-border-color) !important;
  border-right: var(--bs-border-width) solid var(--bs-border-color) !important;
}

// 
.bootstrap-table .fixed-table-container.fixed-height:not(.has-footer) {
  border-bottom: var(--bs-border-width) solid var(--bs-border-color) !important;
}

// 
.bootstrap-table .fixed-table-container.fixed-height .table thead th {
  border-bottom: none !important;
}

//
.bootstrap-table
  .fixed-table-container
  .fixed-table-body
  .fixed-table-loading
  .loading-wrap
  .animation-dot,
.bootstrap-table
  .fixed-table-container
  .fixed-table-body
  .fixed-table-loading
  .loading-wrap
  .animation-wrap::after,
.bootstrap-table
  .fixed-table-container
  .fixed-table-body
  .fixed-table-loading
  .loading-wrap
  .animation-wrap::before {
  background-color: var(--bs-light-text) !important;
}

.bootstrap-table
  .fixed-table-container
  .fixed-table-body
  .fixed-table-loading
  .loading-wrap
  .loading-text {
  color: var(--bs-light-text) !important;
}

//  checkbox: true,bootstrap
.bootstrap-table
  .fixed-table-container
  .table
  .bs-checkbox
  label
  input[type='checkbox'] {
  @include bs-checkbox;
}

//  showColumns:true  
.bootstrap-table .keep-open .dropdown-item-marker input[type='checkbox'] {
  @include bs-checkbox;
}

// showToggle: true 
.bootstrap-table .card-views .card-view label input[type='checkbox'] {
  @include bs-checkbox;
}

// 
.bootstrap-table.fullscreen {
  background: var(--bs-body-bg) !important;
  padding: 12px !important;
}

// 
.bootstrap-table .pagination {
  flex-wrap: wrap !important;
}

// fix:
.fixed-columns,
.fixed-columns-right {
  background-color: var(--bs-body-bg);
}
